import React from 'react';
import styles from './Contacts.module.css'
import styleContainer from "../common/styles/containerStyle.module.css";

export const Contacts = () => {
    return (
        <div className={styles.contactsBlock}>
            <div className={`${styleContainer.container} ${styles.contactsContainer}`}>
                <h3 className={styles.title}>My contacts</h3>
                <form className={styles.contactsForm}>
                    <input type={'text'} placeholder={'Your name'}/>
                    <input type={'text'} placeholder={'Your mail/phone number'}/>
                    <textarea type={'text'} placeholder={'Type your message here'}>
                    </textarea>
                </form>
                <button>Send</button>
            </div>
        </div>
    );
};

